<template>
    <div>
        <div class="m-author-header">
            <div class="m-box">
                <Avatar class="u-author-avatar" :uid="uid" :url="avatar" :size="avatarSize" :frame="avatar_frame" />
                <div class="u-author-info">
                    <span class="u-name">
                        {{ data.display_name || "匿名" }}&nbsp;<span class="u-uid">(UID : {{ data.ID || 0 }})</span>
                    </span>
                    <div class="u-tips">
                        <el-tooltip :content="`当前经验 ${data.experience || 0}`" placement="top">
                            <span class="u-level" :class="'lv-' + level" :style="{backgroundColor:showLevelColor(level)}">Lv.{{ level }}</span>
                        </el-tooltip>
                        <el-tooltip :content="vipTypeTitle" v-if="isPRO || isVIP" placement="top">
                            <a class="u-vip" href="/vip/premium?from=user_homepage" target="_blank">
                                <i class="u-icon vip">{{ vipType }}</i>
                            </a>
                        </el-tooltip>
                        <el-tooltip content="签约作者" v-if="isSuperAuthor" placement="top">
                            <span class="u-superauthor">
                                <i class="u-icon superauthor">签约作者</i>
                            </span>
                        </el-tooltip>
                    </div>
                    <div class="u-info" :title="authorInfo.user_bio||'这个人太懒了~没有写签名。'">
                        {{ authorInfo.user_bio||'这个人太懒了~没有写签名。' }}
                    </div>
                </div>
            </div>
            <div class="m-focus">
                <div class="m-btn-box">
                    <el-button icon="el-icon-plus" class="m-btn u-btn-attention" v-if="!isFollow" @click="follow" size="mini">关注TA</el-button>
                    <div class="m-btn u-already-attention" v-else >
                        <el-button class="u-btn" size="mini" @mouseenter.native="attentionText='取消关注'" @mouseleave.native="attentionText='已关注'" @click="unfollow">{{ attentionText }}</el-button>
                        <el-button class="u-btn u-btn-disabled" size="mini"  :disabled="true">发消息</el-button>
                    </div>
                </div>

                <div class="u-more">
                    <el-popover
                        placement="bottom-start"
                        trigger="click"
                        width="90"
                        v-model="moreOperate"
                    >
                        <a href="/feedback" target="_blank">
                            <el-button size="mini" class="u-more-btn">举报</el-button>
                        </a><br>
                        <el-button size="mini" class="u-more-btn" @click="joinBlacklist">拉黑</el-button>
                        <svg width="6" height="20" viewBox="0 0 6 20" fill="none" xmlns="http://www.w3.org/2000/svg" v-bind:svg-inline="''" v-bind:slot="'reference'" v-bind:class="'u-more-img'" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path d="M3 20a2.5 2.5 0 110-5 2.5 2.5 0 010 5zm0-7.5a2.5 2.5 0 110-5 2.5 2.5 0 010 5zM3 5a2.5 2.5 0 110-5 2.5 2.5 0 010 5z" fill="#fff"/></svg>
                    </el-popover>
                </div>
            </div>

        </div>

        <div class="m-author-primary">
            <!--    左右分栏模式，左侧作品，右侧相关信息-->
            <div class="m-left">
                <Left class="m-common-box u-content"></Left>
            </div>
            <div class="m-right">
                <!--        加入时间-->
                <div class="m-common-box m-join-box">
                    <div class="u-join">
                        <i class="u-icon u-icon-join">
                            <svg class="octicon octicon-link" viewBox="0 0 16 16" width="16" height="16" aria-hidden="true" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path fill-rule="evenodd" d="M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z"/></svg>
                        </i>
                        <span>加入于 {{ data.user_registered | time }}</span>
                    </div>
                    <div class="u-fans">
                        <i class="u-icon u-icon-fans">
                            <svg class="icon" viewBox="0 0 1024 1024" xmlns="http://www.w3.org/2000/svg" width="16" height="16" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path d="M667.787 117.333c165.077 0 270.88 132.374 270.88 310.528 0 138.251-125.099 290.507-371.574 461.59a96.768 96.768 0 01-110.186 0C210.432 718.368 85.333 566.112 85.333 427.86c0-178.154 105.803-310.528 270.88-310.528 59.616 0 100.054 20.832 155.787 68.096 55.744-47.253 96.17-68.096 155.787-68.096zm0 63.147c-41.44 0-70.262 15.19-116.96 55.04-2.166 1.845-14.4 12.373-17.942 15.381a32.32 32.32 0 01-41.77 0c-3.542-3.018-15.776-13.536-17.942-15.381-46.698-39.85-75.52-55.04-116.96-55.04-126.026 0-206.88 100.779-206.88 246.219 0 110.901 113.526 248.544 344.299 408.128a32.352 32.352 0 0036.736 0C761.141 675.253 874.667 537.6 874.667 426.699c0-145.44-80.854-246.219-206.88-246.219z" fill="#666"/></svg>
                        </i>粉丝数 <b>{{fansNum}}</b>
                    </div>
                </div>

                <Primary @authorInfo="getAuthorInfo"></Primary>
            </div>
        </div>
    </div>
</template>

<script>
import { follow, unfollow } from "@jx3box/jx3box-common-ui/service/follow";
import { __userLevelColor } from "@jx3box/jx3box-common/data/jx3box";
import { user as medal_map } from "@jx3box/jx3box-common/data/medals.json";
import frames from "@jx3box/jx3box-common/data/user_avatar_frame.json";
import User from "@jx3box/jx3box-common/js/user";
import { getFansCount } from "@jx3box/jx3box-common-ui/service/follow";
import {tvLink} from "@jx3box/jx3box-common/js/utils";
import dateFormat from "@/utils/dateFormat";
import {deny,undeny} from "@/service/author";
import Left from "./Left"
import  Primary from './Primary';
export default {
    name: "Me",
    components:{
        Left,
        Primary
    },
    data: function () {
        return {
            medals: [],
            medal_map,
            frames,
            isVIP: false,
            isFollow:false,
            attentionText:'已关注',
            moreOperate:false,
            fansNum:0,
            authorInfo:{},
            avatarSize:'m'
        };
    },
    computed: {
        uid: function () {
            return this.$store.state.uid;
        },
        data: function () {
            return this.$store.state.userdata;
        },
        avatar: function () {
            return this.data.user_avatar || "";
        },
        avatar_frame: function () {
            return this.data.user_avatar_frame || "";
        },
        isPRO: function () {
            return this.data?.is_pro;
        },
        isSuperAuthor: function () {
            return !!this.data?.sign;
        },
        vipType: function () {
            return this.isPRO ? "PRO" : "PRE";
        },
        vipTypeTitle: function () {
            return this.isPRO ? "专业版会员用户" : "高级版会员用户";
        },
        level: function () {
            return User.getLevel(this.data?.experience || 0);
        },
        tv_link: function () {
            return tvLink(this.data.tv_type, this.data.tv_id) || "";
        },
        tv_img: function () {
            return __imgPath + "image/tv/" + this.data.tv_type + ".png";
        },
        frameUrl: function () {
            return __imgPath + `image/avatar/${this.avatar_frame}/${this.avatar_frame}.svg`;
        },
        isLogin: function () {
            return User.isLogin();
        },
    },
    filters:{
        time: (val) => {
            return dateFormat(new Date(val));
        },
    },
    methods: {
        // 关注
        follow() {
            if (!this.isLogin) {
                User.toLogin();
                return;
            }
            follow(this.uid)
                .then((res) => {
                    this.$message.success("关注成功");
                    this.isFollow = true;
                    this.loadFans()
                })
                .catch((err) => {
                    console.log(err);
                });
        },
        // 取消关注
        unfollow() {
            this.$confirm("确定不再关注此人？", "提示", {
                confirmButtonText: "确定",
                cancelButtonText: "取消",
                type: "warning",
            }).then(() => {
                unfollow(this.uid)
                    .then((res) => {
                        this.$message.success("取关成功");
                        this.isFollow = false;
                        this.loadFans();
                    })
                    .catch((err) => {
                        console.log(err);
                    });
            }).catch(_ => {});;
        },
        showLevelColor : function (level){
            return __userLevelColor[level]
        },
        // 格式化粉丝数
        formatFansNum(num) {
            if (num < 10000) {
                return num === 0 ? "" : num;
            } else {
                return (num / 10000).toFixed(1) + "万";
            }
        },
        loadFans(){
            getFansCount(this.$store.state.uid).then(res=>{
                this.fansNum = res.data.data.follower_count || 0;
                this.isFollow = res.data.data.is_followed;
            })
        },
        getAuthorInfo(v){
            this.authorInfo=v
        },
        //拉黑
        joinBlacklist(){
            this.$confirm("确定要拉黑此人？", "提示", {
                confirmButtonText: "确定",
                cancelButtonText: "取消",
                type: "warning",
            }).then(() => {
                deny(this.uid).then(() => {
                    this.$message.success("拉黑成功");
                }) .catch((err) => {
                    console.log(err);
                });
            }).catch(_ => {});;

        },
        avatarSizeChange(){
            let w=document.body.clientWidth || document.documentElement.clientWidth
            if(w>500){
                this.avatarSize='l'
            }else{
                this.avatarSize='m'
            }
        }
    },
    created() {
        this.avatarSizeChange()
    },
    mounted: function () {
        if (this.uid) {
            this.loadFans();
        }
        let _this=this
        window.onresize = function () {
            _this.avatarSizeChange();
        }
    },
}
</script>

<style scoped lang="less">
@import "../../assets/css/newCss/me.less";
@import "../../assets/css/newCss/primary.less";
</style>
